<template>
  <b-modal v-model="showModal" @hidden="onHidden" @show="onShow" hide-footer title="Asset" size="lg">
      <div class="row">
        <div class="col-sm-6">
    
    
      
      <div class="form-group">    
                <label for="name">Name</label>
                <input class="form-control" id="name" type="text" v-model="document.name" >
            </div>
            <div class="form-group">    
                <label for="longText">Long text</label>
                <input class="form-control" type="text" id="longText" v-model="document.longText">
            </div>
      <legend>Properties</legend>
         <div class="form-group" v-for="property in properties">
                <div class="form-group">
                     <label :for="property.propertyReference" >{{property.propertyName}}</label>
                    <input class="form-control" :type="property.type" :id="property.propertyReference" :name="property.propertyReference" :value="property.value" v-model="property.value">
                   
                </div>
            </div>
                </div>
          <div class="col-sm-6">
    
  
    <a v-b-toggle.location>add location</a>
   <b-collapse id="location">
        <div class="form-group">
            <label for="longitude">Longitude</label>
            <input class="form-control" type="text" id="longitude" name="longitude"  >       
        </div>
        <div class="form-group">
            <label for="latitude">Latitude</label>
            <input class="form-control" type="text" id="latitude" name="latitude"  >       
        </div>
       <button class="btn btn-primary" @click="addLocation">Add</button>
    </b-collapse>
             </div>
    
    </div>  
        <div id="mfooter">
            <button  class="btn btn-outline-secondary" @click="closeModal()"> Close </button>
            <button type="button" class="btn btn-success" data-dismiss="modal" @click="updateDocument">Save</button>
            <button type="button" class="btn btn-danger" @click="deleteDocument">Delete</button>
    </div>
    </b-modal>
</template>


<script>
    import _ from 'lodash'
    import Vue from 'vue'
    import BootstrapVue from 'bootstrap-vue'
    Vue.use(BootstrapVue);
    import DocumentTypeService from '../../../services/DocumentTypeService'
    import DocumentService from '../../../services/DocumentService'
    export default {

        name: 'ViewDocumentModal',
        props: {
            docRef: {
                type: String,
                required: true
            },
            show: {
                type: Boolean,
                required: true
            },
            indexRef: {
                type: String,
                required: true
            }
        },
        data() {
            return {
                document: {},
                properties: [],
                showModal: false,
                locations: []

            }
        },
        watch: {
            show: function() {
                this.showModal = this.show
            }
        },

        methods: {
            deleteDocument: async function(){
                var securityToken = this.$store.state.securityToken;
                await DocumentService.deleteDocument(this.indexRef, this.docRef, securityToken).then((response)=>{
                   this.$emit('updated',{
                                       'indexRef' : this.indexRef,
                                       'doc' : this.document, 
                                       'properties' : this.properties
                                    })   
                })
            },
            closeModal: function() {
                this.$emit('updated');
            },
            addLocation: async function(){
                this.$emit('locationAdded',{"location": 
                                                {
                                                 "latitude":"50.700003", 
                                                 "longitude":"-2.447132"
                                                },
                                           "indexRef":this.indexRef,
                                           "documentRef":this.docRef
                                          })
            },
            updateDocument: async function() {
                
                
                this.$emit('update',{
                                       'indexRef' : this.indexRef,
                                       'reference' : this.docRef,
                                       'doc' : this.document, 
                                       'properties' : this.properties
                                    }) 
                
               /*
                var securityToken = this.$store.state.securityToken;
                await DocumentService.updateDocument(this.indexRef, this.docRef, updateData, securityToken).then((response) => {
                    console.log("document updated")
                     this.createProperties()
                    
                    
                }) 
                */
            },
             createProperties: async function(){
                
                var propertiesSubmitted = [];
                
                this.properties.forEach((p) => {
                    var prop = {
                        'reference': p.propertyReference,
                        'value': p.value
                    };
                    console.log("properties obj data: " + prop)
                    propertiesSubmitted.push(prop);
                })
                
                var securityToken = this.$store.state.securityToken;
                
                await DocumentService.setDocumentProperties(this.indexRef, this.docRef, propertiesSubmitted,securityToken).then((response)=>{  
                     console.log("properties updated")
                    this.publishDocument(response.data.reference)
                   
                })
            },
            publishDocument: async function(){
                var securityToken = this.$store.state.securityToken;
               await  DocumentService.publishLatestVersion(this.indexRef,this.docRef,securityToken).then((response)=>{
                   console.log(response)
                     console.log("document published")
                   this.document = {}
                    this.properties = {}
                    this.showModal = false
                    this.$emit('updated')
               })
            },
            onHidden(evt) {
                this.document = {}
                this.properties = {}
                this.showModal = false
                this.$emit('close')
            },
            getDocumentType: async function() {
                // await DocumentTypeService.getType(this.indexRef,this.document.documentType).then((response)=>{
                //  if(response.data.documentTypeProperties != null)
                //this.properties = response.data.documentTypeProperties
                // })

            },
            getDocument: async function() {
                await DocumentService.getDocument(this.indexRef, this.docRef).then((response) => {
                    this.document = response.data
                    var p = this.document.publishedVersion.properties
                    this.properties = this.document.publishedVersion.properties
                })
            },
            onShow(evt) {
                //this.getDocumentType();
                this.getDocument();

            }
        },
        beforeMount() {

        }
    }

</script>

<style scoped lang="scss">
    
   
    h2.display-4 {
        font-size: 32px;
    }

</style>



