<template>
<div id="menu" v-bind:class="{'show-search-form': showSearchForm}">
    <h2>Search Options</h2>
    <hr>
    <form>
       <!--
        <legend><a data-toggle="collapse" href="#collapsePrice" role="button" aria-expanded="false" aria-controls="collapsePrice" id="priceButton" class="collapsed">Maximum Price</a></legend>
        <div class="collapse multi-collapse" id="collapsePrice">
            <div class="form-group">
                <select id="price" name="price" class="form-control" v-model="price">
                    <option value="">none</option>
                    <option value="22">&pound; 22</option>
                    <option value="35">&pound; 35</option>
                    <option value="70">&pound; 70</option>
                    <option value="500">&pound; 500</option>
                    <option value="1000">&pound; 1000</option>
                    <option value="1500">&pound; 1500</option>
                    <option value="2000">&pound; 2000</option>
                    <option value="2500">&pound; 2500</option>
                    <option value="3000">&pound; 3000</option>
                </select>
            </div>
        </div>
        <hr>
-->
       
        <!--
        <legend><a data-toggle="collapse" href="#collapseTypes" role="button" aria-expanded="false" aria-controls="collapseTypes" id="typesButton" class="collapsed">Types</a></legend>

        <div class="collapse multi-collapse" id="collapseTypes">

-->
        <legend>Types</legend>
            <div class="form-group">
                <div class="multiple-choice">
                    <input type="checkbox" class="form-control" id="roundabout" v-model="roundabout">
                    <label for="roundabout" id="longLabel">Roundabout</label>
                </div>
            </div>
            <div class="form-group">
                <div class="multiple-choice">
                    <input type="checkbox" class="form-control" id="parkingtickets" v-model="parkingtickets">
                    <label for="parkingtickets" id="longLabel">Parking Tickets</label>
                </div>
            </div>
        <!--
        </div>
        
        <hr>
        <legend><a data-toggle="collapse" href="#collapsePlaces" role="button" aria-expanded="false" aria-controls="collapsePlaces" id="placesButton" class="collapsed">Places *not working yet</a></legend>
        <div class="collapse multi-collapse" id="collapsePlaces">
            <div class="form-group">
                <div class="multiple-choice">
                    <input type="checkbox" class="form-control" id="dorchester">
                    <label for="dorchester" id="longLabel">Dorchester</label>
                </div>
            </div>
            <div class="form-group">
                <div class="multiple-choice">
                    <input type="checkbox" class="form-control" id="weymouth">
                    <label for="weymouth" id="longLabel">Weymouth</label>
                </div>
            </div>
        </div>
-->
        <hr>

        <div class="form-group">
            <div class="multiple-choice">
                <input type="checkbox" class="form-control" id="available">
                <label for="unavailable" id="longLabel">Include Currently sponsored</label>
            </div>
        </div>
        <hr>

        <button class="btn btn-success mt-4" @click.prevent="search">Search</button>
    </form>
</div>
</template>

<script>
    import Vue from 'vue'
    import Vuex from 'vuex'
    import api from '../../../store/api.js'


 
    export default {
        name: 'SearchOptions',
        props: {
            indexRef: {
                type:String,
                required: true
            }
        },
        watch: {
            searchCriteria: {
                handler: function() {
                    this.$store.dispatch('search',this.indexRef);
                },
                deep: true
            }
        },
        methods: {
            search: function() {
                this.$store.dispatch('search');
                if (this.showSearchForm)
                    this.$store.commit("toggleSearchForm");
            }
        },
        computed: {
            searchCriteria() {
                return this.$store.state.searchCriteria;
            },
            showSearchForm() {
                return this.$store.state.showSearchForm
            },
            roundabout: {
                get() {
                    return this.$store.state.searchCriteria.documentTypes.find(type => type == "roundabout");
                },
                set(value) {
                    var documentTypeParam = {
                        reference: "roundabout",
                        value: value
                    };
                    this.$store.dispatch("setDocumentTypeParameter", documentTypeParam);
                }
            },
            parkingtickets: {
                get() {
                    return this.$store.state.searchCriteria.documentTypes.find(type => type == "parking-tickets");
                },
                set(value) {
                    var documentTypeParam = {
                        reference: "parking-tickets",
                        value: value
                    };
                    this.$store.dispatch("setDocumentTypeParameter", documentTypeParam);
                }
            },
            price: {
                get() {
                    return this.$store.getters.getParameter("price");
                },
                set(value) {
                    var param = {
                        "reference": "price",
                        "value": value
                    }
                    this.$store.dispatch("setSearchParamater", param)
                }
            }
        }
    }

</script>


<style scoped lang="scss">
    #collapsePrice {
        margin-bottom: .5rem;
    }

    #typesButton,
    #placesButton,
    #priceButton {
        font-size: 19px;
        padding-left: 30px;
        color: black;
        &:hover {
            text-decoration: none;
        }
        &:before {
            content: '-';
            position: absolute;
            left: 10px;
            margin-top: 3px;
            transition: transform .1s;

        }

        &.collapsed {
            &:before {
                content: '+';
                position: absolute;
                left: 10px;
                margin-top: 3px;
                transition: transform .1s;



            }
        }
    }

    h2 {
        font-size: 22px;
    }

    #menu {

        #longLabel {
            font-size: 16px;
            padding-left: 2px;
        }
        &.show-search-form {
            left: 0;
        }
        position: absolute;
        width: 97vw;
        top: 220px;
        left: -200vw;

        transition: left .5s;
        background: white;
        padding: 10px;
        z-index: 2;
        margin-bottom: 20px;
        border-bottom: 20px solid white;
        &.open {
            left: 0px;
        }
        form {
            label {
                padding-bottom: 0;
                line-height: 1.2;
            }
            #placesToggle,
            #typesToggle {
                margin-bottom: 5px;
                display: block;
                &:focus {
                    text-decoration: none;
                }
                &:hover {
                    text-decoration: none;
                }
                &:visited {
                    text-decoration: none;
                }
                &:before {
                    content: '+';
                    top: 5px;
                    position: relative;
                }
                &[aria-expanded=true] {
                    &:before {
                        content: '-';
                        top: 5px;
                        position: relative;
                    }
                }
            }
        }
    }

    #menuOpen {

        &.open {

            &:before {
                content: url(../../../assets/images/close.svg);
            }
        }
        &:before {
            content: url(../../../assets/images/settings-sm.svg);
            margin-right: 1px;
        }
        &:after {
            content: 'Search';
        }
        &.open:after {
            content: 'Close';
        }
    }

    #searchMenuHolder {
        width: 100%;
        height: 80px;
        position: relative;
    }

    @media only screen and (min-width: 800px) {

        #menu {
            position: relative;
            width: 40%;
            left: 0;
            top: 0;
            background: white;
            border-right: solid 1px grey;
            padding-right: 30px;
        }
        #menuOpen {
            display: none;
        }
    }

</style>



