<template>      
    <ModalTemplate v-if="show" >
        <h3 slot="header" class="modal-title">Edit index</h3>
        <div slot="body">
            <div class="form-group">    
                <label for="name">Name</label>
                <input class="form-control" id="name" type="text" v-model="viewIndex.indexName" >
            </div>        
            <div class="form-group">    
                <label for="longText">Long text</label>
                <input class="form-control" type="text" id="longText"  v-model="viewIndex.indexLongText">
            </div>     
            <div class="form-group">    
                <label for="reference">Reference</label>
                <input class="form-control" id="reference" >
            </div>
        </div>         
        <div slot="footer" style="width:100%">
            <button type="button" class="btn btn-outline-secondary" @click="closeView"> Close </button>
            <button type="button" class="btn btn-success pull-right" data-dismiss="modal" @click="updateIndex">Save</button>
            <button type="button" class="btn btn-danger pull-right" data-dismiss="modal" @click="deleteIndex">Delete</button>
        </div>
    </ModalTemplate>
</template>


<script>
    import IndexService from '../../../services/IndexService'
    import ModalTemplate from '../ModalTemplate'
    export default {

        name: 'ViewIndexModal',
        components: {
            ModalTemplate
        },
        props: {
            index: {
                type: Object,
                required: true
            },
            show: {
                type: Boolean,
                requried: true
            }

        },
        data() {
            return {
                viewIndex: {
                    indexName: "",
                    indexLongText: ""
                },

                showModal: false,
                showDeleteModal: false
            }
        },
        methods: {
            closeView: function(){
              this.$emit('close')  
            },
            deleteIndex: async function() {
                await IndexService.deleteIndex(this.indexRef).then((response) => {
                 
                    this.$router.push("/admin/indexes/");
                }, (error) => {
                    console.log("error deleting index");
                    console.log("E: " + error);
                })
            },
            updateIndex: async function() {
                var updateProperties = {
                    "name": this.viewIndex.indexName,
                    "longText": this.viewIndex.indexLongText
                }
                await IndexService.updateIndex(this.index.reference, updateProperties)
                    .then((response) => {
                        this.closeModal();
                        console.log("ref sending: " + response.data.reference)
                        this.$emit('update', response.data.reference)
                    }, (error)=>{
                        console.log('error updating index');
                        console.log('E' + error);
                    })
            }
        }
    }

</script>

<style scoped lang="scss">
    h2.display-4 {
        font-size: 32px;
    }

</style>



