// Production
var _security = axios.create({
  baseURL: 'http://web.dorsetcc.gov.uk/catalogue/v1/public/users/login',  
  timeout: 5000
});

var _ = axios.create({
  baseURL: 'http://web.dorsetcc.gov.uk/catalogue/v1/indexes',
  timeout: 5000
});

export default {
    get(url){

        return _.get(url)
    },
    
    post(url,payload,authToken){
        
        console.log('payload: ' + payload)
           var headers = {
                      headers: {
                            Authorization:  'Bearer ' + authToken
                      }
                   };
        return _.post(url,payload,headers)
    },
    postSearch(url,payload){
        return _.post('http://52.56.188.219/catalogue/v1/search/index/advertise-dorset',payload)
    },
     put(url,payload,authToken){ 
         var headers = {
                      headers: {
                            Authorization:  'Bearer ' + authToken
                      }
                   };
         
        return _.put(url,payload,headers)
    },
    
    delete(url,authToken){
        var headers = {
                      headers: {
                            Authorization:  'Bearer ' + authToken
                      }
                   };
        return _.delete(url,headers)
    },
    
    postSecurityCredentials(credentials){
        return _security.post("",credentials)
    }
}


