import api from './api'

export default{
    
    getIndexes(){
       return api.get("")
    },
    getIndex(indexRef){
        console.log("get index service: " + indexRef)
        return api.get(indexRef)
    },
    createIndex(index){
       return api.post("",index)
    },
    updateIndex(indexRef,index){
        return api.put(indexRef,index)
    },
    deleteIndex(indexRef){
        return api.delete(indexRef)
    }
    
}


