<template>
    <div id="searchCriteria" >

        <div v-for="documentType in searchCriteria.documentTypes" class="label label-primary tag-label">
            <span class="tag-item">{{documentType}}</span>
            <span class="delete show" @click="removeDocumentType(documentType)" tabindex="0" title="Remove search criteria">X</span>     
        </div>
        
        <div v-for="property in searchCriteria.parameters" class="label label-primary tag-label">
           <span class="tag-item">
               <span v-if="property.reference=='price'">Max </span>{{property.reference}}: <span v-if="property.reference=='price'">&pound;</span>{{property.value}}
           </span>
            <span class="delete show" @click="removeProperty(property.reference)" tabindex="0" title="Remove search criteria">X</span>
        </div>  

       <div v-if="searchCriteria.location" class="label label-primary tag-label">
           <span class="tag-item">Postcode: {{postcode}}
           </span>
           <span class="delete show" @click="removePostcodeSearch" tabindex="0" title="Remove search criteria">X</span>
    </div>  
</div>
</template>

<script>
    import Vuex from 'vuex'
    import Toolbar from './Toolbar'
    export default {
        name: 'SearchCriteria',
        props: [
            "parameters",
            "documnetTypes",
            "location"
        ],
        methods: {
            removeProperty: function(propertyToRemove) {
                // remove hyphens
                propertyToRemove = propertyToRemove.replace(/-/g, "")
                this.$store.dispatch("removeSearchCriteriaProperty", propertyToRemove)
            },
            removeDocumentType: function(type) {
                this.$store.dispatch("removeSearchDocumentType", type)
            },
            removePostcodeSearch: function() {
                this.$store.dispatch("removeLocationSearch");
            }
        },
        computed: {
            isPostcodeSearch: {
                get() {
                    return (this.$store.state.postcode != null && this.$store.state.postcode.trim() != "");
                }
            },
            postcode: function() {
                return this.$store.state.postcode;
            },
            searchCriteria: {
                get() {
                    return this.$store.state.searchCriteria;
                }
            }

        }
    }

</script>

<style scoped lang="scss">
    #searchCriteria {
        display: flex;
        height: 30px;
        margin-bottom: 15px;


        .label {
            font-size: 16px;
            float: left;
            position: relative;
            display: inline-block;
            margin-left: 20px;
            font-weight: normal;
            padding: 2px;
            margin-bottom: 5px;
            padding-right: 25px;
            border-radius: 0;
            transition: all 1s;
            border-bottom: solid 1px lightgrey;

            .tag-item {
                padding-right: 5px;
                &:focus {
                    outline: orange 1px solid;
                    +.delete {
                        background-color: darken(#337ab7, 10%);
                        color: white;
                    }
                }
            }

            .delete {
                position: absolute;
                top: 0;
                right: 0;
                display: inline-block;
                margin-left: 0;
                padding: 5px;
                font-size: 14px;

                &:hover,
                &:focus {
                    cursor: pointer;
                    color: darkred;

                }
                transition: all 0.5s;
            }
        }
    }

    .parameter {
        font-size: 14px;
        margin-right: 25px;
        color: darkgreen;
        &.unmatched {
            text-decoration: line-through;
            color: darkred;
        }
    }

    @media only screen and (min-width: 800px) {

        #searchCriteria {
            margin-left: 25px;
        }
    }

</style>



