<template>
    <div id="searchResultsContainer">
       <Toolbar></Toolbar>
        <SearchCriteria></SearchCriteria>
      <transition name="component-fade" mode="out-in">
        <Map v-show="view == 'mapView'" :assets="assets"></Map>
    </transition>
        <div class="result-cards" v-bind:class="{'grid-view': view == 'gridView'}" v-show="view == 'listView' || view == 'gridView'" v-if="assetsExist">  
            <div class="result-card" v-for="asset in assets" v-bind:title="asset.FIELD2">
                <router-link :to="{ path: '/' + asset.document.index + '/' + asset.document.reference}" class="card-link">
                    <div class="card-heading">
                        <div class="icon"><img :src="getIcon(asset.document.documentType)">
                        </div>
                        <div class="heading"><div id="heading-text">{{asset.document.name}}</div></div>
                    </div>
                  
                    
                    <div class="card-main">
                          
                        {{asset.document.longText}}
                        
                        <p class="price" v-for="property in asset.document.publishedVersion.properties" v-if="property.propertyReference == 'price'">&pound;{{property.value | round()}}</p>
                      <p v-if="isLocationSearch">{{asset.distanceFromCoordinate | round()}} miles from {{postcode}}</p>
                        <span class="parameter" v-for="matchingParam in asset.matchingParameters">{{matchingParam.name}}</span>
                        <span class="parameter unmatched" v-for="param in asset.nonMatchingParameters">{{param.name}}</span>
                   </div>
               </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import Map from './Map.vue'
    import Toolbar from './Toolbar'
    import SearchCriteria from './SearchCriteria'

    export default {
        name: 'Assets',
        components: {
            Map,
            Toolbar,
            SearchCriteria
        },
        methods:{
          getIcon(documentType){
              return require("../../../assets/images/icons/" + documentType + ".svg");
          }  
        },
        computed: {
            view() {
                return this.$store.state.view
            },
            assets() {
                return this.$store.getters.getSearchResults;
            },
            assetsExist() {
                return this.$store.getters.numberOfResults > 0
            },
            postcode(){
                return this.$store.state.postcode
            },
            isLocationSearch(){
                return this.$store.state.postcode != null &&  this.$store.state.postcode.trim() != "";
            }

        },
        filters: {
            round:  function(value) {
                    if (!value) {
                        value = 0;
                    }

                    
                      var  decimals = 1;
                  value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals);
  return value;
                
                }
            }
    }
</script>

<style scoped lang="scss">
    #searchResultsContainer {
        width: 100%;
        .result-cards {

            width: 100%;
            .result-card {
                border: solid 1px darkgrey;
                box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
                padding: 10px;
                margin-bottom: 15px;
                width: 100%;

                &:hover {
                    outline: orange solid 1px;
                }

                .card-heading {
                    color: black;
                    display: flex;
                    flex-wrap: nowrap;
                    padding-bottom: 5px;
                    border-bottom: 1px #E9E9E9 solid;
                    .heading {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        font-size: 22px;
                    }

                    .icon {

                        width: 50px;
                        height: 50px;
                        margin-right: 10px;

                        display: block;
                        width: 50px;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        img {
                            display: block;
                            width: 50px;
                            height: 50px;
                        }
                    }
                }
                .card-main {
                    color: black;
                    padding-top: 5px;
                    padding-left: 10px;
                    padding-right: 10px;
                    .price {
                        text-align: right;
                    }
                }
            }
        }
    }


    .icon {
        &.parking-tickets-icon:before {
            content: url(../../../assets/images/icons/parking-tickets.svg);
        }
        &.roundabout-icon:before {
            content: url(../../../assets/images/icons/roundabout.svg);
        }
    }

    @media only screen and (min-width: 800px) {
        #searchResultsContainer {
            margin-left: 20px;

            .result-cards {

                display: flex;
                flex-wrap: wrap;
                justify-content: flex-end;
                .result-card {
                    width: 90%;
                }
            }
            .grid-view {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .result-card {
                    width: 48%;
                }
            }
        }

    }

</style>



