<template>
   <div>
       <ol class="breadcrumb">
    <li class="breadcrumb-item" aria-current="page"><router-link to="/">Home
                </router-link></li>
     <li class="breadcrumb-item" aria-current="page"><router-link :to="{ path: '/' + indexRef + '/search'}">Search Results</router-link></li>
            
                 <li class="breadcrumb-item active" aria-current="page">{{document.reference}}</li>
  </ol>
       <div class="document-header">
           <div class="icon-holder">
                <img id="brandImage" :src="getIcon(document.documentTypeReference)">
           </div>
      <div class="document-headings">
       <h1>{{document.name}}</h1>
       <h2>{{document.longText}}</h2>
         
         
          </div>
           </div>
       <h3>&pound;{{assetPrice | round()}}</h3>
        <div class="available-tag" v-if="document.publishedVersion.properties.available">This opportunity is available</div>
        <div v-else class="available-tag not"><p>Currently sponsored. <a href="#">Join waiting list</a></p>
    
    </div>
       <hr>

       <p v-for="p in document.publishedVersion.properties" v-if="p.display && p.propertyReference != 'price'">{{p.propertyName}} : {{p.value}}</p>
       <p v-if="document.publishedVersion.properties.available">This opportunity is available</p>
       <p v-else>This opportunity is currently taken</p>
       <hr>
       <h3>Location</h3>
       
       
            <AssetMaps :locations="document.publishedVersion.locations" :streetView="false"></AssetMaps>
       <hr>
       
            <p>Contact our marketing team</p>
            <p> <a href="#">marketing@dorsetcc.gov.uk</a> </p>
            <p> <a href="#" rel="tel">01305 224125</a>
    </p>
            <p>quoting reference <strong>{{document.reference}}</strong></p>
    </div>
    
   
</template>

<script>
    import AssetMaps from './AssetMaps/AssetMaps'
    import DocumentService from '../../../services/DocumentService'
    export default {
        name: 'AssetView',
        props: ['indexRef', 'documentRef'],
        data() {
            return {
                document: {}
            }
        },
        components: {
            AssetMaps
        },
        methods: {
            getIcon(documentType) {
                return require("../../../assets/images/icons/" + documentType + ".svg");
            },
            getDocument: function() {
                DocumentService.getDocument(this.indexRef,this.documentRef).then(response => {
                    this.document = response.data;
                })
            }
        },
        computed: {
              streetViewRequired(){
                  return !(this.document.publishedVersion.locations[0].streetviewLatitude == null);
              },
            assetPrice(){
                var price = null
                this.document.publishedVersion.properties.forEach((p)=>{
                    if(p.propertyReference == 'price')
                       price = p.value
                    console.log("p: " + p.propertyReference )
                })
                return price
            }
        },
        beforeMount() {
            this.getDocument();
        },        
        filters: {
            round:  function(value) {
                    if (!value) {
                        value = 0;
                    }

                    
                      var  decimals = 1;
                  value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals);
  return value;
                
                }
            }

    }

</script>

<style scoped lang="scss">
    h1 {
        font-size: 1.8rem;
        margin-bottom: 2px;
    }

    h2 {
        font-size: 1.2rem;
    }

    p {
        font-size: 1rem;
        margin-bottom: 2px;
        margin-top: 2px;
    }

    .document-header {
        display: flex;
        width: 100%;
        flex-direction: row-reverse;
        justify-content: space-between;
        .icon-holder {
            height: 100px;
            margin-right: 20px;
            width: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            img {
                width: 75px;
            }
        }
    }

    .available-tag {
        font-size: 14px;
        p {
            margin-bottom: 2px;
        }
        button {
            font-size: 14px;
        }
    }

    @media only screen and (min-width: 700px) {

        .document-header {
            .icon-holder {
                width: 100px;
                height: 100px;
            }
        }
        h1 {
            font-size: 1.8rem;
            margin-bottom: 5px;
        }
        h2 {
            font-size: 1.4rem;
        }
        p {
            font-size: 19px;
            margin-bottom: 2px;
            margin-top: 2px;
        }
        .breadcrumb {
            font-size: 19px;

        }
    }

</style>



