<script>
    export default {
        name: 'AdFooter'
    }

</script>

<style scoped lang="scss">
    @font-face {
        font-family: 'dorsetforyou';
        src: url("../assets/fonts/dorsetforyou-eot.eot");
        src: url("../assets/fonts/dorsetforyou-eot.eot") format("embedded-opentype"), url("../assets/fonts/dorsetforyou-ttf.ttf") format("truetype"), url("../assets/fonts/dorsetforyou-woff.woff") format("woff");
        font-weight: normal;
        font-style: normal
    }

    footer {
        width: 100%;
        padding: 20px 15px;
        margin-top: 30px;
        background-color: #f2f2f2;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        font-size: 12px;
    }

    
        #footerLogo {
            width: 100%;
            display: flex;
            justify-content: center;
            svg {
                width: 300px;
                height: 50px;
            }
        }

        #links {
            display: flex;
            justify-content: flex-end;
        }
        #textLinks {
            width: 90%;
            display: flex;
            flex-wrap: wrap;
            margin-top: 20px;
        
            font-size: 12px;
            justify-content: center;

            li {
                width: 45%;
                &.list-inline-item:not(:last-child) {
                    margin-right: 0;
                }
                #dfyLText{
               
                    position: absolute;
                    left:-3000px;
                }

            }
        }
        .link {
            font-size: 14px;

        }

        #socialList {
         margin-top:30px;
            display: flex;
            flex-wrap: nowrap;
            width: 100%;
            justify-content: center;
            li {
                width: 50px;
            }

            .social-link {
                color: #CB436A;
                font-family: dorsetforyou;
                font-size: 25px;
                line-height: .6;
                &:hover {
                    color: red;
                }

                &.twitter {
                    &:before {
                        content: "";
                    }
                }
                &.facebook {
                    &:before {
                        content: "";
                    }
                }

                &.instagram {
                    &:before {
                        content: "";
                    }
                }
                &.youtube {
                    &:before {
                        content: "";
                    }
                }
                &.flickr {
                    &:before {
                        content: ""
                    }
                }
            }
        }
  


    @media only screen and (min-width: 600px) {
         #footerLogo {
        
            svg {
                height:80px;
             }
        }
        
        #links {
            display: flex;
            justify-content: center;
        } 
        #socialList{
            margin-top:0px;
        }
        #textLinks {
            height:40px;
                width: 100%;
            max-width:592px;
                display: flex;
                justify-content: space-around;
                li {
                    width: auto;
                    height:  30px;
                      #dfyLText{
               
                    position: relative;
                    left:0;
                }
                }
            }
    }

    @media only screen and (min-width: 700px) {
        #textLinks {
            width: 80%;
        }
    }
    
        @media only screen and (min-width: 1000px) {
        #textLinks {
            width: 60%;
        }
    }
    
          @media only screen and (min-width: 1200px) {
         #links {
            display: flex;
            justify-content: center;
        }
        #textLinks {
            width: 50%;
        }
    }



</style>



