

<template>
  <b-modal v-model="show" hide-footer title="Create index"> 
            <div class="form-group">    
                <label for="name">Name</label>
                <input class="form-control" id="name" type="text" v-model="index.indexName" >
            </div>        
            <div class="form-group">    
                <label for="longText">Long text</label>
                <input class="form-control" type="text" id="longText"  v-model="index.indexLongText">
            </div>     
            <div class="form-group">    
                <label for="reference">Reference</label>
                <input class="form-control" id="reference" >
            </div>
       
        <div id="mfooter">
            <button type="button" class="btn btn-outline-secondary" @click="close"> Close </button>
            <button type="button" class="btn btn-success" data-dismiss="modal" @click="createIndex">Create Index</button>
          
        </div>
    </b-modal>
</template>

<script>
    import Vue from 'vue'
    import BootstrapVue from 'bootstrap-vue'
    Vue.use(BootstrapVue);
    import IndexService from '../../../services/IndexService'
  
    export default {

        name: 'NewIndexModal',
        props: { 
            show: {
                type:Boolean,
                required:true
            }
        },
        data() {
          return {
              index: {
                name: null,
                longText: null
              }
          }  
        }, 
  
        methods:
        {
             createIndex: async function() {
                 var obj = {
                     name: this.index.name,
                     longText: this.index.longText
                 }
                await IndexService.createIndex(obj).then((response)=>{
                    this.$emit('created');
                },(error)=>{
                    console.log("error creating index")
                    console.log("E: " + error)
                })
            },
            close: function(){
                this.$emit("close")
            }
        }
    

   
    }
</script>

<style scoped lang="scss">
  

</style>



