<!-- Map component to show search results  -->

<template>
<div id="map">

    </div>


</template>



<script>
    export default {
        name: 'Map',
        props: ['assets'],
        watch: {
            
         
            assets: function(newVal, oldVal) {
                console.log("search results change " + this.markers.length);
                this.setMarkers();
            }
        },
        data() {
            return {
                map: {},
                markers: [],   
                // marker positions used to centre map
                bounds:new google.maps.LatLngBounds()
            }
        },
        methods: {

            // loop through locations adding marker for each
            setMarkers: function() {
            
                for (var i = 0; i < this.markers.length; i++) {
                    this.markers[i].setMap(null);   
                }
                this.markers.length = 0;
                this.assets.forEach((asset) => {
                    if (asset.document.publishedVersion.locations != null && asset.document.publishedVersion.locations.length > 0) {
                        var price = asset.document.publishedVersion.properties.find(function(element){
                            return element.propertyReference == 'price'
                        })
                        var infoContent = '<p class="lead">' + asset.document.name + '</p><p class="lead">' + price.value + '</p>'
                        this.addMarker(asset.document.publishedVersion.locations[0],infoContent)
                    }
                })
                this.fitMapToBounds();
                this.map.setZoom(10);
               
            },
            // Sets the map on all markers in the array.
            setMapOnAll: function(map) {
                for (var i = 0; i < this.markers.length; i++) {
                    this.markers[i].setMap(map);

                }
            },
            addMarker: function(location,infoContent) {
                var position = new google.maps.LatLng(location.latitude, location.longitude);
                var marker = new google.maps.Marker({
                    position: position,
                    map: this.map
                });
                

                var infowindow = new google.maps.InfoWindow({
                    content: infoContent
                });
                marker.addListener('click', function() {
                    infowindow.open(this.map, marker);
                });  
                this.markers.push(marker);
                

            },
            fitMapToBounds(){
               this.markers.forEach((marker) =>{
                    this.bounds.extend(marker.position)
               }) 
                 this.map.fitBounds(this.bounds);
               
            },
            clearMarkers: function() {
                setMapOnAll(null);
            },
            setMapOnAll: function(gmap) {
                for (var i = 0; i < markers.length; i++) {
                    markers[i].setMap(gmap);
                }
            },
            deleteMarkers: function() {
                

            }
        },
   
        mounted() {
            
            // Create initial map with no markers
            const element = document.getElementById('map');
            const options = {
                zoom: 10,
                center: new google.maps.LatLng(50.563110932545825, -2.4489365380248693),
                gestureHandling: 'greedy'
            };
            this.map = new google.maps.Map(element, options);
            
            // set markers if any to set
            if(this.assets != null && this.assets.length > 0)
                this.setMarkers();
        }
    }

</script>


<style scoped lang="scss">
    #map {
        width: 100%;
        height: 60vh;
        box-shadow: 12px 12px 12px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }

</style>



