<template>
   <div>
        <h2 class="display-4">{{indexRef}} assets</h2>
       <transition name="component-fade" mode="out-in">
         <b-list-group>   
           <b-list-group-item :button="true" v-for="document in documents" :key="document.id"  @click="openDocumentViewModal(document)">
                  {{document.name}}
        </b-list-group-item>
           </b-list-group>
        </transition>

       <button class="btn btn-primary mt-3"  @click="openNewDocumentModal()">Create new asset</button>
        
       <NewDocumentModal  :indexRef="indexRef" :show="showNewDocumentModal" @created="onCreated" @close="onCloseNewDocumentModal" @create="create"></NewDocumentModal>
        
       <ViewDocumentModal :docRef="currentDocumentRef" :indexRef="indexRef" :show="showDocumentViewModal" @updated="onDocumentUpdated" @deleted="onDocumentDeleted" @close="onCloseDocumentViewModal" @update="updateDocument" @locationAdded="addLocation"></ViewDocumentModal>                                                                                          
 
       
    </div>
</template>


<script>
    import Vue from 'vue'
    import BootstrapVue from 'bootstrap-vue'
    Vue.use(BootstrapVue);
    import IndexService from '../../../services/IndexService'
    import DocumentService from '../../../services/DocumentService'
    import ViewDocumentModal from './ViewDocumentModal'
    import NewDocumentModal from './NewDocumentModal'
    export default {
        
        name: 'DocumentsTab',
        props: {
            indexRef: {
                type: String,
                required: true
            },
            documents:{
                type:Array,
                required:true
            }
        },
        components: {
            ViewDocumentModal,NewDocumentModal
        },
        data() {
            return {
                currentDocumentRef: "",
                showDocumentViewModal: false,
                showNewDocumentModal:false
            }
        },

        methods: {
            onDocumentDeleted(){
                this.$emit('updated')
                this.currentDocumentRef = ""
                this.showDocumentViewModal = false
            },
            onDocumentUpdated(){
                this.$emit('updated')
                this.currentDocumentRef = ""
                this.showDocumentViewModal = false
            },
            openNewDocumentModal(){
                this.showNewDocumentModal = true
            },
            onCloseNewDocumentModal(){
                this.showNewDocumentModal = false  
            },
            onCreated(){
                 this.showNewDocumentModal = false
                this.$emit('updated')
            },
            openDocumentViewModal(doc){
                 this.currentDocumentRef = doc.reference
                this.showDocumentViewModal = true
            },
            onCloseDocumentViewModal(){
                this.currentDocumentRef = ""
                this.showDocumentViewModal = false
            },
            create(newDocument){
    
             console.log("name: " + newDocument.doc.name)
            
                var securityToken = this.$store.state.securityToken;
          
                var created = DocumentService.createDocument(newDocument, securityToken)
                if(created){
                    this.showNewDocumentModal = false
                    this.$emit('updated');
                }

            },
            updateDocument(updatedDocument){
                var securityToken = this.$store.state.securityToken;
                var updated = DocumentService.updateDocument(updatedDocument, securityToken)
            },
            async addLocation(locationRequest){
                console.log("r: " + locationRequest.documentRef)
                var securityToken = this.$store.state.securityToken;
                await DocumentService.createLocation(locationRequest,securityToken).then(async(response)=>{
                    await DocumentService.publishLatestVersion(locationRequest.indexRef,locationRequest.documentRef,securityToken).then((response)=>{
                        console.log("published location")
                    })
                })  
            }
        }
    }


</script>

<style scoped lang="scss">
    h2.display-4{
        font-size:32px;
    }

</style>



